.list-products {
  padding: 100px 0 204px;
  background: url('../img/bg/4.webp') center center no-repeat;
  background-size: cover;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &--cards {
    padding: 100px 0 165px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__container {
    @include glass(1167px);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__title {
    font: 700 62px/1.17741935483871 $font-default;
    color: $color-blue;
    margin: 0 0 16px;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__descr {
    font: 700 28px/1.178571428571429 $font-default;
    color: $color-gray;
    margin: 0 0 116px;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      justify-content: flex-start;
      flex-wrap: wrap;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__item {
    margin: 0 20px 0 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      width: 50%;
      margin: 0 0 0 0;
      padding: 37px 59px 47px 59px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--pink {
      background: $color-pink-light;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--blue-light {
      background: $color-blue;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--blue {
      background: $color-blue-3;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--purple {
      background: $color-purple;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &:last-child {
      margin-right: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0 0 48px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      margin: 0 0 41px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__item-title {
    font: 700 24px/1.166 $font-default;
    color: $color-gray;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:after {
      content: '';
      @include icon-semicircle-blue-3;
      margin: 34px 0 19px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products--cards & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--pink & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--blue-light & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-semicircle-white;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } }
    .list-products__item--blue & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-semicircle-white;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } }
    .list-products__item--purple & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-semicircle-white;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } } }
  &__item-descr {
    font: 400 18px/1.171666666666667 $font-default;
    color: $color-gray;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .list-products--cards & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--pink & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--blue-light & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--blue & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .list-products__item--purple & {
      color: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } } }
