.slider {
  padding: 55px 0 53px;
  background: url('../img/bg/4.webp') center center no-repeat;
  background-size: cover;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &__container {
    @include glass(1240px);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__title {
    font: 700 62px/1.17741935483871 $font-default;
    color: $color-blue;
    margin: 0 0 16px;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__descr {
    font: 700 28px/1.178571428571429 $font-default;
    color: $color-gray;
    margin: 0 0 31px;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__wrapper {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__subtitle {
    font: 700 28px/1.178571428571429 $font-default;
    color: $color-gray;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    transform: translate3d(0,-84px,0);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:after {
      content: '';
      @include icon-semicircle-blue;
      margin: 34px 0 35px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__text {
    font: 400 22px/1.181818181818182 $font-default;
    color: $color-gray;
    text-align: center;
    transform: translate3d(0,-84px,0);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 0 26px;
    transform: translate3d(0,-84px,0);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .slick-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    li {
      display: block;
      text-decoration: none;
      text-indent: -9999px;
      width: 14px;
      height: 14px;
      border: 1px solid $color-gray-5;
      border-radius: 7px;
      background-color: transparent;
      padding: 0;
      margin: 0 6px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &.slick-active {
        width: 30px;
        height: 30px;
        border-radius: 15px;
        border: 2px solid $color-purple;
        background-color: transparent;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } } }
  .slick-arrow {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .swiper-pagination {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .swiper-pagination-bullet {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__slide {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__slide-inner-wrapper {
    background: $color-white;
    border: 1px solid $color-blue;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    border-radius: 10px;
    padding: 0 32px 11px 32px;
    height: 100%;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .slick-track {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .slick-list {
    padding: 84px 0 68px 0;
    margin: 0 -34px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  .slick-slide {
    margin: 0 34px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__nav {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__nav-item {
    position: absolute;
    width: 76.05px;
    height: 76.05px;
    background: $color-purple;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    border: none;
    outline: none;
    text-indent: -9999px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--prev {
      position: absolute;
      top: 50%;
      left: -76px;
      transform: translate3d(0,calc(-50% + 42px),0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-triangle-left;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } }
    &--next {
      position: absolute;
      top: 50%;
      right: -76px;
      transform: translate3d(0,calc(-50% + 42px),0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-triangle-right;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } } } }
