.manual {
  padding: 212px 0 258px;
  background: url('../img/bg/5.webp') center center no-repeat;
  background-size: cover;
  @include breakpoint($tablet-1024) {
    padding: 142px 0 205px;
    background: url('../img/bg/5-1.webp') center center no-repeat; }
  @include breakpoint($tablet-768) {
    padding: 142px 0 205px;
    background: url('../img/bg/5-1.webp') center center no-repeat; }
  @include breakpoint($mobile-360) {
    padding: 35px 0 67px;
    background: url('../img/bg/5-2.webp') center center no-repeat; }
  &--cards {
    padding: 212px 0 324px;
    @include breakpoint($tablet-1024) {
      padding: 142px 0 205px; }
    @include breakpoint($tablet-768) {
      padding: 142px 0 205px; }
    @include breakpoint($mobile-360) {
      padding: 35px 0 67px; } }
  &__container {
    @include glass(1240px);
    @include breakpoint($tablet-1024) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center; }
    @include breakpoint($tablet-768) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center; }
    @include breakpoint($mobile-480) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center; }
    @include breakpoint($mobile-360-480) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center; }
    @include breakpoint($mobile-360) {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding-left: 15px;
      padding-right: 15px; }
    .manual--cards & {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__title {
    @include title;
    margin: 0 0 16px;
    text-align: center;
    @include breakpoint($tablet-1024) {
      margin-bottom: 19px; }
    @include breakpoint($tablet-768) {
      margin-bottom: 19px; }
    @include breakpoint($mobile-360) {
      margin: 0 0 26px; }
    .manual--cards & {
      @include breakpoint($tablet-1024) {
        margin-bottom: 19px; }
      @include breakpoint($tablet-768) {
        margin-bottom: 19px; }
      @include breakpoint($mobile-360) {
        margin: 0 0 26px; } } }
  &__descr {
    font: 700 18px/1.171666666666667 $font-default;
    color: $color-gray;
    margin: 0 0 135px;
    text-align: center;
    @include breakpoint($tablet-1024) {
      line-height: 1.171666666666667;
      margin: 0 0 84px;
      max-width: 350px; }
    @include breakpoint($tablet-768) {
      line-height: 1.171666666666667;
      margin: 0 0 84px;
      max-width: 350px; }
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360) {
      margin: 0 0 34px;
      max-width: 250px;
      font-size: 14px;
      line-height: 1.172142857142857; }
    .manual--cards & {
      @include breakpoint($tablet-1024) {
        line-height: 1.171666666666667;
        margin: 0 0 84px; }
      @include breakpoint($tablet-768) {
        line-height: 1.171666666666667;
        margin: 0 0 84px; }
      @include breakpoint($mobile-360) {
        margin: 0 0 37px; } } }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    @include breakpoint($tablet-1024);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-480) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding-left: 0;
      padding-right: 0;
      max-width: 100%; }
    @include breakpoint($mobile-360-480) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding-left: 0;
      padding-right: 0;
      max-width: 100%; }
    @include breakpoint($mobile-360) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding-left: 0;
      padding-right: 0;
      max-width: 100%; }
    .manual--cards & {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding-left: 0;
        padding-right: 0;
        max-width: 100%; }
      @include breakpoint($mobile-360-480) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding-left: 0;
        padding-right: 0;
        max-width: 100%; }
      @include breakpoint($mobile-360) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding-left: 0;
        padding-right: 0;
        max-width: 100%; } } }
  &__list-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 2.5% 0 0;
    width: 23.125%;
    @include breakpoint($tablet-1024);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-480) {
      width: 100%;
      margin: 0 0 49px 0; }
    @include breakpoint($mobile-360-480) {
      width: 100%;
      margin: 0 0 49px 0; }
    @include breakpoint($mobile-360) {
      margin: 0 0 49px 0;
      width: 60.975%; }
    &:last-child {
      margin: 0 0 0 0; }
    .manual--cards & {
      background: $color-white;
      box-shadow: 0 4px 4px rgba($color-black, 0.25);
      border-radius: 10px;
      padding: 40px 45px 54px;
      cursor: pointer;
      margin: 0 2.5% 0 0;
      width: 23.125%;
      @include breakpoint($tablet-1024) {
        box-shadow: 0 0 14px rgba($color-black, 0.25);
        padding: 28px 18px 34px; }
      @include breakpoint($tablet-768) {
        box-shadow: 0 0 14px rgba($color-black, 0.25);
        padding: 28px 18px 34px; }
      @include breakpoint($mobile-480) {
        box-shadow: 0 0 14px rgba($color-black, 0.25);
        padding: 28px 18px 34px;
        width: 100%;
        margin: 0 0 27px 0; }
      @include breakpoint($mobile-360-480) {
        box-shadow: 0 0 14px rgba($color-black, 0.25);
        width: 100%;
        margin: 0 0 27px 0; }
      @include breakpoint($mobile-360) {
        width: 77.5%;
        margin: 0 0 27px 0;
        padding: 26px 26px 40px 26px; }
      &:hover {
        padding: 51px 39px 49px 39px;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480) {
          padding: 26px 26px 40px 26px; }
        @include breakpoint($mobile-360-480) {
          padding: 26px 26px 40px 26px; }
        @include breakpoint($mobile-360) {
          padding: 26px 26px 40px 26px; } }
      &:last-child {
        margin: 0 0 0 0; } } }
  &__list-item-row {
    height: 100%;
    @include breakpoint($tablet-1024);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .manual--cards & {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--first {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      .manual--cards & {
        display: block;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480) {
          display: none; }
        @include breakpoint($mobile-360-480) {
          display: none; }
        @include breakpoint($mobile-360) {
          display: none; } }
      .manual__list-item:hover & {
        display: none;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480);
        @include breakpoint($mobile-360-480);
        @include breakpoint($mobile-360); } }
    &--second {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      .manual--cards & {
        display: none;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480) {
          display: block; }
        @include breakpoint($mobile-360-480) {
          display: block; }
        @include breakpoint($mobile-360) {
          display: block; } }
      .manual__list-item:hover & {
        display: block;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480);
        @include breakpoint($mobile-360-480);
        @include breakpoint($mobile-360); } } }
  &__img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 0 27px;
    padding: 0 40px;
    @include breakpoint($tablet-1024) {
      margin: 0 0 32px;
      padding: 0 30px; }
    @include breakpoint($tablet-768) {
      margin: 0 0 32px;
      padding: 0 20px; }
    @include breakpoint($mobile-480) {
      margin: 0 0 23px; }
    @include breakpoint($mobile-360-480) {
      margin: 0 0 23px; }
    @include breakpoint($mobile-360) {
      padding: 0 14px;
      margin: 0 0 23px; }
    .manual--cards & {
      @include breakpoint($tablet-1024) {
        margin: 0 0 50px; }
      @include breakpoint($tablet-768) {
        margin: 0 0 50px; }
      @include breakpoint($mobile-480) {
        margin: 0 0 50px; }
      @include breakpoint($mobile-360-480) {
        margin: 0 0 50px; }
      @include breakpoint($mobile-360) {
        margin: 0 0 23px; } }
    &--mobile-hidden {
      display: block;
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480) {
        display: none; }
      @include breakpoint($mobile-360-480) {
        display: none; }
      @include breakpoint($mobile-360) {
        display: none; } }
    &--mobile-visible {
      display: none;
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480) {
        display: block; }
      @include breakpoint($mobile-360-480) {
        display: block; }
      @include breakpoint($mobile-360) {
        display: block; } } }
  &__subtitle {
    font: 700 24px/1.172 $font-default;
    color: $color-blue;
    text-transform: uppercase;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 10px;
    @include breakpoint($tablet-1024) {
      font-size: 19px;
      line-height: 1.172105263157895; }
    @include breakpoint($tablet-768) {
      font-size: 19px;
      line-height: 1.172105263157895; }
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360) {
      font-size: 14px !important;
      line-height: 1.172142857142857 !important; }
    .manual--cards & {
      font: 700 18px/1.166666666666667 $font-default;
      color: $color-blue;
      padding: 0 0;
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360); }
    &:after {
      content: '';
      @include icon-semicircle-blue-4;
      margin: 27px 0 28px;
      @include breakpoint($tablet-1024) {
        margin: 17px 0 20px; }
      @include breakpoint($tablet-768) {
        margin: 17px 0 20px; }
      @include breakpoint($mobile-480) {
        margin: 10px 0 10px; }
      @include breakpoint($mobile-360-480) {
        margin: 10px 0 10px; }
      @include breakpoint($mobile-360) {
        margin: 11px 0 8px; } }
    .manual__list-item-row--first & {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      &:after {
        display: none;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480);
        @include breakpoint($mobile-360-480);
        @include breakpoint($mobile-360); } }
    .manual__list-item-row--first & {
      @include breakpoint($tablet-1024);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      &:after {
        margin: 40px 0 40px;
        @include breakpoint($tablet-1024);
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-480);
        @include breakpoint($mobile-360-480);
        @include breakpoint($mobile-360); } } }
  &__text {
    font: 400 18px/1.1716 $font-default;
    color: $color-gray;
    text-align: center;
    padding: 0 10px;
    @include breakpoint($tablet-1024) {
      font-size: 13px;
      line-height: 1.171538461538462; }
    @include breakpoint($tablet-768) {
      font-size: 13px;
      line-height: 1.171538461538462; }
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.172142857142857; }
    .manual--cards & {
      padding: 0 0;
      @include breakpoint($tablet-1024) {
        font-size: 16px;
        line-height: 1.171538461538462; }
      @include breakpoint($tablet-768) {
        font-size: 16px;
        line-height: 1.171538461538462; }
      @include breakpoint($mobile-360) {
        font-size: 14px;
        line-height: 1.172142857142857; } } } }
