.header {
  width: 100%;
  background: $color-blue;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &__container {
    @include glass(1398px);
    max-width: 100%;
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__col {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:last-child {
      margin-left: auto;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__logo {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__img {
    width: 100%;
    height: auto;
    display: block;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 0 9.722222222222222vw;
    height: 100%;
    @include breakpoint($tablet-768) {
      margin: 0 0 0 56px; }
    @include breakpoint($mobile-360); }
  &__list-item {
    font: 700 18px/1.166666666666667 $font-default;
    @include fluid-type(768px, 1440px, 16px, 18px);
    color: $color-blue-2;
    text-decoration: none;
    margin: 0 73px 0 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    @include breakpoint($tablet-768) {
      margin: 0 81px 0 0; }
    @include breakpoint($mobile-360) {
      font-size: 22px; }
    &:last-child {
      margin-right: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--active {
      color: $color-yellow;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 4px;
        background: $color-white;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } } }
  &__user {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:after {
      content: '';
      @include icon-circle-arrow-bottom;
      margin: 0 0 0 17px;
      @include breakpoint($tablet-768) {
        margin: 0 0 0 16px; }
      @include breakpoint($mobile-360); } }
  &__user-title {
    font: 400 18px/1.166666666666667 $font-default;
    @include fluid-type(768px, 1440px, 16px, 18px);
    color: $color-white;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
