@mixin icon-arrow-bottom {
  @include icon;
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.697.36 4.97 4.568l4.27-4.21' stroke='%232061E6'/%3E%3C/svg%3E");
  width: 10px;
  min-width: 10px;
  height: 6px;
  min-height: 6px;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360) {
    width: 5px;
    min-width: 5px;
    height: 3px;
    min-height: 3px; } }
