.popup {
  display: none;
  height: 100%;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360-480) {
    padding-bottom: 21px; }
  @include breakpoint($mobile-360) {
    padding-bottom: 21px; }
  &--active {
    display: block;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__item {
    @include glass(560px);
    background: $color-white;
    box-shadow: 0 4px 64px rgba($color-blue-4, 0.3);
    border-radius: 10px;
    padding: 85px 93px 94px 93px;
    z-index: 9999;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0);
    @include breakpoint($tablet-768) {
      padding: 37px 93px 77px; }
    @include breakpoint($mobile-480) {
      max-width: calc(100% - 30px);
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($mobile-360-480) {
      max-width: calc(100% - 30px);
      padding-left: 15px;
      padding-right: 15px;
      top: 21px;
      left: 0;
      position: relative;
      transform: translate3d(0,0,0); }
    @include breakpoint($mobile-360) {
      max-width: calc(100% - 30px);
      padding: 26px 15px 30px 15px;
      top: 21px;
      left: 0;
      position: relative;
      transform: translate3d(0,0,0); }
    @include breakpoint($mobile-320) {
      max-width: calc(100% - 30px);
      padding-left: 15px;
      padding-right: 15px; }
    &--indent-top-big {
      padding-top: 116px;
      @include breakpoint($tablet-768) {
        padding-top: 82px; }
      @include breakpoint($mobile-360) {
        padding-top: 18px; } }
    &--special-inner-indent {
      padding-left: 74px;
      padding-right: 74px;
      @include breakpoint($tablet-768) {
        padding-left: 40px;
        padding-right: 40px; }
      @include breakpoint($mobile-360-480) {
        padding-left: 15px;
        padding-right: 15px; }
      @include breakpoint($mobile-360) {
        padding-left: 15px;
        padding-right: 15px; } } }
  &__close {
    display: block;
    cursor: pointer;
    width: 45px;
    height: 45px;
    border-radius: 22.5px;
    border: 4px solid $color-gray-5;
    background: $color-white;
    position: absolute;
    right: 16px;
    top: 16px;
    @include breakpoint($tablet-768) {
      border-color: $color-gray-8; }
    @include breakpoint($mobile-360) {
      display: none; }
    &:after {
      content: '';
      @include icon-close;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%,-50%,0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__title {
    font: 700 40px/1.575 $font-default;
    color: $color-blue;
    margin: 0 0 8px;
    text-align: center;
    @include breakpoint($tablet-768) {
      line-height: 1; }
    @include breakpoint($mobile-360) {
      font-size: 32px;
      line-height: 1.1;
      margin: 0 0 1px; }
    @include breakpoint($mobile-320) {
      font-size: 30px; }
    &--indent-bottom-big {
      margin: 0 0 21px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__descr {
    font: 400 19px/1.157894736842105 $font-default;
    color: $color-gray;
    text-align: center;
    margin: 0 0 23px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.571428571428571;
      font-weight: 400; } }
  &__row {
    margin: 0 0 23px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      margin: 0 0 15px; }
    &--space-between {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360-480) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start; }
      @include breakpoint($mobile-360) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start; } }
    &--align-center {
      display: flex;
      align-items: center;
      justify-content: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--indent-bottom-big {
      margin-bottom: 33px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__input-text {
    @include default-text-input;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__btn {
    @include btn-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 16px;
      padding: 12px 15px;
      font-weight: 700; } }
  &__checkbox-container {
    font: 400 16px/1.375 $font-default;
    color: $color-gray;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-custom {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-link {
    text-decoration: underline;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__link {
    font: 400 16px/1.375 $font-default;
    color: $color-blue;
    text-decoration: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480) {
      @include btn-white;
      width: 100%;
      margin: 0 0 12px;
      max-width: 100%;
      &:last-child {
        margin-bottom: 0; } }
    @include breakpoint($mobile-360) {
      @include btn-white;
      width: 100%;
      margin: 0 0 12px;
      max-width: 100%;
      &:last-child {
        margin-bottom: 0; } }
    &--link-mobile {
      @include breakpoint($mobile-360) {
        font: 400 16px/1.375 $font-default;
        color: $color-blue;
        text-decoration: none;
        padding: 0 0 0 0;
        margin: 0 0 0 0;
        background: none;
        border: none; } } }
  &__overlay {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9998;
    background: rgba($color-black, 0.9);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress {
    margin: 0 auto 33px auto;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--step-first {
      @include icon-progress-step-first;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--step-second {
      @include icon-progress-step-second;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__checkbox-gender {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-gender-input {
    opacity: 0;
    visibility: hidden;
    display: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-gender-pseudo-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-gender-pseudo-item {
    width: 50%;
    border: 1px solid $color-blue;
    font: 700 18px/1.166 $font-default;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    cursor: pointer;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:first-child {
      border-right: none;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--female {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      .popup__checkbox-gender-input + .popup__checkbox-gender-pseudo-wrapper & {
        background: $color-blue;
        color: $color-white;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); }
      .popup__checkbox-gender-input:checked + .popup__checkbox-gender-pseudo-wrapper & {
        background: $color-white;
        color: $color-gray;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } }
    &--male {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      .popup__checkbox-gender-input + .popup__checkbox-gender-pseudo-wrapper & {
        background: $color-white;
        color: $color-gray;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); }
      .popup__checkbox-gender-input:checked + .popup__checkbox-gender-pseudo-wrapper & {
        background: $color-blue;
        color: $color-white;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } } } }
