.participants {
  padding: 46px 0 75px;
  background-color: $color-purple-2;
  background-image: url('../img/bg/3.webp');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &__container {
    @include glass(1230px);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__title {
    font: 700 51px/1.176470588235294 $font-default;
    color: $color-white;
    margin: 0 0 93px;
    text-align: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__row {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--bg-white {
      position: relative;
      height: 840px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--align-center {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__inner-wrapper {
    @include glass(1116px);
    background: $color-white;
    border: 1px solid $color-blue;
    border-radius: 10px;
    padding: 33px 55px 33px 55px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__form {
    margin: 0 0 15px;
    position: relative;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__btn-form {
    @include icon-search;
    position: absolute;
    left: 9px;
    top: 50%;
    transform: translate3d(0,-50%,0);
    background-color: transparent;
    outline: none;
    border: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text-input {
    border: 1px solid $color-gray-5;
    font-size: 14px;
    line-height: 1;
    padding: 12px 30px 12px 30px;
    width: 100%;
    max-width: 330px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__table {
    margin: 0 0 44px 0;
    padding: 0 0 0 0;
    width: 100%;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__table-tbody {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__table-thead {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__table-tr {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .participants__table-thead & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .participants__table-tbody & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__table-td {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .participants__table-thead & {
      border-bottom: 1px solid $color-gray-4;
      padding: 18px 0 18px;
      font: 700 18px/1.166666666666667 $font-default;
      color: $color-purple-2;
      vertical-align: top;
      text-align: left;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    .participants__table-tbody & {
      border-bottom: 1px dashed $color-gray-4;
      padding: 19px 0 20px;
      font: 400 18px/1.166666666666667 $font-default;
      color: $color-gray;
      vertical-align: top;
      text-align: left;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--bold {
      font: 700 18px/1.166666666666667 $font-default !important;
      color: $color-gray;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--percent-21 {
      width: 22%;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--percent-28 {
      width: 28%;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__btn {
    @include btn-white;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
