// GRID : begin
// main parent container (glass, container)
@mixin glass($max-width) {
  width: 100%;
  max-width: $max-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  //@include breakpoint($tablet-768)
  //@include breakpoint($mobile-360)
  //  padding-left: 15px
 }  //  padding-right: 15px
// GRID : end

// ICON
@mixin icon {
  display: block;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain; }

// FORM ELEMENTS : begin
// placeholder
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content; }
  &::-moz-placeholder {
    @content; }
  &:-moz-placeholder {
    @content; }
  &:-ms-input-placeholder {
    @content; }
  &:focus::-webkit-input-placeholder {
    color: transparent; }
  &:focus::-moz-placeholder {
    color: transparent; }
  &:focus:-moz-placeholder {
    color: transparent; }
  &:focus:-ms-input-placeholder {
    color: transparent; } }
// text input default
@mixin default-text-input {
  border: 1px solid $color-blue;
  border-radius: 0;
  padding: 22px 14.18px 21.67px;
  font-family: $font-default;
  color: $color-gray;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  user-select: none;
  width: 100%;
  outline: none;
  margin: 0;
  display: block;
  text-align: left;
  background: $color-white;
  transition: all 0.3s ease;
  @include breakpoint($tablet-768) {
    padding: 20px 14px 19px; }
  @include breakpoint($mobile-360) {
    font-size: 14px;
    padding: 12.5px 13.19px 11.5px 13.19px; }
  @include placeholder {
    color: $color-gray-2;
    transition: all 0.3s ease; }
  &:focus {
    outline: none;
    color: $color-black;
    border-color: rgba($color-black, 0.5);
    transition: all 0.3s ease;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &--error {
    color: $color-red;
    transition: all 0.3s ease;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
