// button purple
@mixin btn-purple {
  user-select: none;
  display: inline-block;
  font-family: $font-default;
  font-weight: 700;
  font-size: 14px;
  text-transform: none;
  color: $color-white;
  width: 100%;
  height: auto;
  background: $color-purple;
  border: none;
  line-height: 1;
  padding: 14px 42px 14.88px;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  border-radius: 0;
  text-align: center;
  transition: all 0.2s ease;
  @include breakpoint($tablet-768) {
    font-size: 19px;
    padding: 20px 65px 21px 65px; }
  @include breakpoint($mobile-360) {
    font-size: 16px;
    padding: 12px 31px 12px 31px; }
  &:disabled,
  &[disabled] {
    opacity: 0.5;
    cursor: default;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:hover {
      opacity: 0.5;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &:hover {
    background: $color-black;
    color: $color-white;
    transition: all 0.2s ease;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
