// OTHER
@mixin selectionText {
  ::-moz-selection {
    background: $color-purple;
    color: $color-white;
    -webkit-text-fill-color: $color-white;
    -webkit-opacity: 1; }
  ::selection {
    background: $color-purple;
    color: $color-white;
    -webkit-text-fill-color: $color-white;
    -webkit-opacity: 1;
    -webkit-text-fill-color: $color-white; } }
@mixin hover {
  opacity: 1;
  transition: opacity 0.2s ease;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &:hover {
    opacity: 0.75;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
