.qr {
  padding: 157px 0 155px;
  height: 100%;
  @include breakpoint($tablet-768) {
    padding: 58px 0 96px; }
  @include breakpoint($mobile-360) {
    padding: 0 0 35px;
    background: $color-blue-6; }
  &--with-shadow {
    padding: 136px 0 136px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &--bg-blue-tablet {
    @include breakpoint($tablet-768) {
      background: $color-blue-7; }
    @include breakpoint($mobile-360) {
      background: $color-blue-6; } }
  &__container {
    @include glass(1075px);
    @include breakpoint($tablet-768) {
      padding-left: 50px;
      padding-right: 50px; }
    @include breakpoint($mobile-360) {
      padding: 12px 15px 12px 15px;
      border-radius: 10px; }
    .qr--with-shadow & {
      @include glass(1230px);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__inner-wrapper {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .qr--with-shadow & {
      background: $color-white;
      box-shadow: 0 4px 64px rgba($color-blue-4, 0.3);
      border-radius: 10px;
      padding: 109px 135px 127px 95px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__content {
    @include breakpoint($tablet-768) {
      background: $color-white;
      box-shadow: 0 4px 64px rgba($color-blue-4, 0.3);
      border-radius: 10px;
      padding: 24px 81px 91px 81px; }
    @include breakpoint($tablet-640) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-480) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-360-480) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-360) {
      background: $color-white;
      border-radius: 0 0 10px 10px;
      padding: 15px 20px 35px 20px; }
    &--border-radius-all {
      @include breakpoint($mobile-360) {
        border-radius: 10px 10px 10px 10px; } } }
  &__title {
    text-align: center;
    font: 700 62px/1.016129032258065 $font-default;
    color: $color-black;
    margin: 0 0 80px;
    @include breakpoint($tablet-768) {
      line-height: 1;
      margin-bottom: 52px; }
    @include breakpoint($tablet-640) {
      font-size: 46px; }
    @include breakpoint($mobile-480) {
      font-size: 44px; }
    @include breakpoint($mobile-360-480) {
      font-size: 40px; }
    @include breakpoint($mobile-360) {
      font-size: 32px;
      line-height: 1.1;
      margin: 0 0 1px;
      text-align: left; }
    @include breakpoint($mobile-320) {
      font-size: 30px;
      line-height: 1.1;
      margin: 0 0 1px;
      text-align: left; }
    .qr--with-shadow & {
      text-align: left;
      margin: 0 0 83px;
      white-space: nowrap;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--special-indent {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin: 0 0 24px; } }
    &--mobile-indent-bottom-big {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin: 0 0 24px; } } }
  &__title-selected {
    display: inline-block;
    color: $color-blue;
    position: relative;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:after {
      content: ')';
      font: 700 62px/1.016129032258065 $font-default;
      color: $color-blue;
      transform: rotate(90deg);
      position: absolute;
      left: 60px;
      bottom: -43px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        display: none; }
      .qr--right-col-tablet-full-width & {
        @include breakpoint($tablet-768) {
          left: 38px;
          bottom: -43px; }
        @include breakpoint($mobile-360) {
          left: 38px;
          bottom: -43px; } } } }
  &__form {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--download {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__row {
    margin: 0 0 47px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--indent-big-bottom {
      margin-bottom: 80px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--two-cols {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start; } }
    &--align-center {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--tablet-hidden {
      @include breakpoint($tablet-768) {
        display: none; } }
    &--mobile-hidden {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        display: none; } }
    &--mobile-indent-bottom-none {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin-bottom: 0; } } }
  &__col {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--left {
      margin: 0 58px 0 0;
      width: 100%;
      max-width: 465px;
      @include breakpoint($tablet-768) {
        max-width: 50%;
        margin: 0 37px 0 0; }
      @include breakpoint($mobile-360);
      .qr--with-shadow & {
        max-width: 520px;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); }
      .qr__form--download & {
        @include breakpoint($tablet-768) {
          display: none; }
        @include breakpoint($mobile-360); }
      .qr--right-col-tablet-full-width & {
        @include breakpoint($tablet-768) {
          display: none; }
        @include breakpoint($mobile-360) {
          display: none; } } }
    &--right {
      width: 100%;
      @include breakpoint($tablet-768) {
        width: 50%; }
      @include breakpoint($mobile-360);
      .qr--with-shadow & {
        width: 100%;
        @include breakpoint($tablet-768) {
          max-width: 50%; }
        @include breakpoint($mobile-360); }
      .qr--right-col-tablet-full-width & {
        @include breakpoint($tablet-768) {
          width: 100%; }
        @include breakpoint($mobile-360) {
          width: 100%; } } }
    &--align-center {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__img {
    display: block;
    width: 100%;
    height: auto;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--mobile-hidden {
      display: block;
      @include breakpoint($mobile-360) {
        display: none; } }
    &--mobile-visible {
      display: none;
      @include breakpoint($mobile-360) {
        display: block; } } }
  &__text-link {
    text-decoration: none;
    color: $color-blue;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--with-underline {
      text-decoration: underline;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__label {
    background: $color-white;
    border: 24px dashed $color-gray-7;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    border-radius: 10px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include breakpoint($tablet-768) {
      border: none;
      box-shadow: none;
      border-radius: 0; }
    @include breakpoint($mobile-360);
    &--btn {
      @include btn-blue;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      width: 100%;
      max-width: 373px;
      margin: 0 0 40px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--label-download-mobile-hidden {
      @include breakpoint($tablet-768) {
        display: none; }
      @include breakpoint($mobile-360) {
        display: none; } } }
  &__file-input {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text {
    width: 100%;
    font: 400 22px/1 $font-default;
    color: $color-gray;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--text-big {
      font: 400 25px/1.16 $font-default;
      color: $color-gray;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--align-center {
      justify-content: center;
      text-align: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--with-icon {
      padding: 139px 0 0 0;
      position: relative;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint($tablet-768) {
        padding: 0 0 0 0; }
      @include breakpoint($mobile-360);
      &:before {
        content: '';
        @include icon-file-2;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,0,0);
        @include breakpoint($tablet-768) {
          display: none; }
        @include breakpoint($mobile-360); } }
    &--italic {
      font-style: italic;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--indent-bottom-big {
      margin-bottom: 37px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--indent-bottom-big-2 {
      margin-bottom: 42px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--transparent {
      opacity: 0.4;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--mobile-hidden {
      @include breakpoint($tablet-768) {
        display: none; }
      @include breakpoint($mobile-360) {
        display: none; } } }
  &__text-special {
    font: italic 400 22px/1 $font-default;
    color: $color-gray;
    text-align: center;
    padding: 176px 0 0 0;
    position: absolute;
    right: -25px;
    bottom: 30px;
    @include breakpoint($tablet-768) {
      display: none; }
    @include breakpoint($mobile-360);
    &:before {
      content: '';
      @include icon-file-3;
      position: absolute;
      top: 0;
      left: calc(50% - 70px);
      transform: translate3d(-50%,0,0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__checkbox-container {
    font: 400 22px/1 $font-default;
    color: $color-gray;
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 14px; }
    &--align-left {
      justify-content: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--mobile-visible {
      display: none;
      @include breakpoint($tablet-768) {
        display: flex;
        font-size: 18px; }
      @include breakpoint($mobile-360); }
    &--mobile-hidden {
      display: flex;
      @include breakpoint($tablet-768) {
        display: none; }
      @include breakpoint($mobile-360); } }
  &__checkbox {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-custom {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text-link {
    text-decoration: none;
    color: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress-outer-wrapper {
    background: $color-white;
    border: 4px dashed $color-gray-7;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    border-radius: 10px;
    width: 100%;
    height: 100%;
    padding: 43px 30px 50px 46px;
    @include breakpoint($tablet-768) {
      box-shadow: none;
      border: none;
      border-radius: 0;
      padding: 0 0 0 0; }
    @include breakpoint($mobile-360) {
      box-shadow: none;
      border: none;
      border-radius: 0;
      padding: 0 0 0 0; }
    &--indent-bottom-top-none {
      padding: 0 30px 0 46px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--inner-elements-center {
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--indent-mobile-none {
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480) {
        padding-left: 0;
        padding-right: 0; }
      @include breakpoint($mobile-360) {
        padding-left: 0;
        padding-right: 0; }
      @include breakpoint($mobile-320) {
        padding-left: 0;
        padding-right: 0; } } }
  &__file-input-name-wrapper {
    position: relative;
    padding: 0 0 0 126px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 50px;
    @include breakpoint($tablet-768) {
      padding: 0 0 0 108px; }
    @include breakpoint($mobile-360) {
      padding: 0 0 0 88px; }
    &:before {
      content: '';
      @include icon-file;
      position: absolute;
      left: 79px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        left: 54px; } }
    &--hidden {
      display: none;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__file-input-name-text {
    font: 400 22px/1 $font-default;
    color: $color-gray-6;
    @include breakpoint($tablet-768) {
      font-size: 14px; }
    @include breakpoint($mobile-360) {
      font-size: 14px; } }
  &__file-input-name-close {
    width: 58px;
    height: 58px;
    background: $color-white;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate3d(0,-50%,0);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      height: 40px;
      width: 40px; }
    &:after {
      content: '';
      @include icon-close-red;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%,-50%,0);
      @include breakpoint($tablet-768) {
        width: 27px;
        min-width: 27px;
        height: 27px;
        min-height: 27px; }
      @include breakpoint($mobile-360); } }
  &__progress-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 18px;
    @include breakpoint($tablet-768) {
      margin: 0 0 38px; }
    @include breakpoint($mobile-360) {
      width: 100%; }
    &--indent-bottom-none {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--hidden {
      display: none;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--mobile-visible {
      display: none;
      @include breakpoint($tablet-768) {
        display: block; }
      @include breakpoint($mobile-360) {
        display: block; } } }
  &__progress-line-wrapper {
    min-width: 344px;
    width: 100%;
    height: 10px;
    border: 1px solid $color-blue;
    margin: 0 0 17px;
    @include breakpoint($tablet-768) {
      width: 100%;
      min-width: 100%; }
    @include breakpoint($mobile-360) {
      width: 100%;
      min-width: 100%;
      height: 7px;
      &--height-small {}
      height: 7px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--indent-bottom-big {
      margin: 0 0 32px;
      @include breakpoint($tablet-768) {
        margin: 0 0 30px; }
      @include breakpoint($mobile-360) {
        margin: 0 0 12px; } }
    &--mobile-visible {
      display: none;
      @include breakpoint($tablet-768) {
        display: block; }
      @include breakpoint($mobile-360) {
        display: block; } } }
  &__progress-line {
    width: 0;
    height: 100%;
    background: $color-blue;
    animation: fillWidth 1.5s forwards;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress-text {
    font: 400 14px/1.571428571428571 $font-default;
    color: $color-gray;
    padding: 0 0 18px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);

    &--mobile-hidden {
      @include breakpoint($tablet-768) {
        display: none; }
      @include breakpoint($mobile-360); } }
  &__btn {
    @include btn-blue;
    margin: 0 0 40px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      margin: 0 0 22px;
      width: 100%;
      max-width: 100%; }
    &--blue {
      @include btn-blue;
      margin: 0 0 0 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--white {
      @include btn-white;
      margin: 0 0 0 0;
      @include breakpoint($tablet-768) {
        margin: 0 0 20px;
        border-color: $color-blue;
        color: $color-blue;
        font: 700 18px/1 $font-default; }
      @include breakpoint($mobile-360); }
    &--mobile-visible {
      display: none;
      @include breakpoint($tablet-768) {
        display: block; }
      @include breakpoint($mobile-360) {
        display: block;
        margin: 0 0 14px; }
      &:last-child {
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          margin-bottom: 0; } } }
    &--indent-bottom-small {
      @include breakpoint($tablet-768) {
        margin-bottom: 18px; }
      @include breakpoint($mobile-360) {
        margin-bottom: 18px; } } }
  &__drop-area {
    @include icon-file-4;
    margin: 0 0 38px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
