.download {
  padding: 140px 0 220px;
  background: $color-blue-6;
  width: 100%;
  height: 100%;
  @include breakpoint($tablet-768) {
    padding: 66px 0 79px 0; }
  @include breakpoint($mobile-360) {
    padding: 21px 0 21px; }
  &__container {
    @include glass(1045px);
    @include breakpoint($tablet-768) {
      @include glass(692px);
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($mobile-360); }
  &__inner-wrapper {
    background: $color-white;
    box-shadow: 0 4px 64px rgba($color-blue-4, 0.3);
    border-radius: 10px;
    padding: 66px 60px 106px 60px;
    @include breakpoint($tablet-768) {
      padding: 29px 80px 69px 80px; }
    @include breakpoint($mobile-360-480) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-360) {
      padding: 15px 20px 35px 20px; } }
  &__title {
    text-align: center;
    font: 700 62px/1.016129032258065 $font-default;
    color: $color-black;
    margin: 0 0 72px;
    @include breakpoint($tablet-768) {
      font-size: 58px;
      margin-bottom: 43px; }
    @include breakpoint($tablet-640) {
      font-size: 54px;
      margin-bottom: 43px; }
    @include breakpoint($mobile-480) {
      font-size: 52px;
      margin-bottom: 43px; }
    @include breakpoint($mobile-360-480) {
      font-size: 40px;
      margin-bottom: 43px; }
    @include breakpoint($mobile-360) {
      font-size: 32px;
      text-align: left;
      margin: 0 0 33px; }
    @include breakpoint($mobile-320) {
      font-size: 30px;
      text-align: left;
      margin: 0 0 33px; }
    &--indent-bottom-none {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); }
    &--indent-bottom-small {
      margin-bottom: 62px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__title-selected {
    display: inline-block;
    color: $color-blue;
    position: relative;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    &:after {
      content: ')';
      font: 700 62px/1.016129032258065 $font-default;
      color: $color-blue;
      transform: rotate(90deg);
      position: absolute;
      left: 66px;
      bottom: -50px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480) {
        font-size: 32px;
        left: 34px;
        bottom: -29px; }
      @include breakpoint($mobile-360-480) {
        font-size: 32px;
        left: 34px;
        bottom: -29px; }
      @include breakpoint($mobile-360) {
        font-size: 32px;
        left: 34px;
        bottom: -29px; }
      @include breakpoint($mobile-320) {
        font-size: 30px;
        left: 32px;
        bottom: -26px; } }
    &--special {
      &:after {
        bottom: -38px;
        @include breakpoint($mobile-480) {
          bottom: -22px; }
        @include breakpoint($mobile-360-480) {
          bottom: -20px; }
        @include breakpoint($mobile-360) {
          font-size: 32px;
          left: 31px;
          bottom: -18px; }
        @include breakpoint($mobile-320) {
          font-size: 30px;
          left: 28px;
          bottom: -18px; } } } }
  &__form {
    width: 100%;
    max-width: 435px;
    margin: 0 auto;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__row {
    margin: 0 0 37px;
    @include breakpoint($tablet-768) {
      margin: 0 0 48px; }
    @include breakpoint($mobile-360);
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__form-text {
    font: italic 400 22px/1 $font-default;
    color: $color-gray;
    text-align: center;
    margin: 0 0 18px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.571428571428571;
      text-align: left; }
    &--hidden-mobile-devices {
      @include breakpoint($tablet-768) {
        display: none; } } }
  &__form-text-bold {
    font-weight: 700;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__label {
    position: relative;
    width: 100%;
    height: 62px;
    border: 1px solid $color-blue;
    margin: 0 0 17px;
    @include breakpoint($tablet-768) {
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center; }
    @include breakpoint($mobile-360) {
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center; }
    &:before {
      content: '';
      @include icon-download;
      position: absolute;
      left: 13px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      z-index: 1;
      @include breakpoint($tablet-768) {
        display: none; }
      @include breakpoint($mobile-360) {
        display: none; } } }
  &__label-text {
    font: 700 18px/1.166666666666667 $font-default;
    color: $color-blue;
    display: none;
    @include breakpoint($tablet-768) {
      display: block;
      font-size: 15px; }
    @include breakpoint($mobile-360) {
      font-size: 14px;
      display: block; } }
  &__file-input {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    opacity: 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__form-btn {
    @include btn-blue;
    width: 100%;
    max-width: 100%;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-container {
    font: 400 22px/1 $font-default;
    color: $color-gray;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.571428571428571; } }
  &__checkbox {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__checkbox-custom {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text-link {
    text-decoration: none;
    color: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__file-input-name-wrapper {
    position: relative;
    padding: 0 0 0 62px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:before {
      content: '';
      @include icon-file;
      position: absolute;
      left: 30px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--hidden {
      display: none;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__file-input-name-text {
    font: 400 22px/1 $font-default;
    color: $color-gray-6;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__file-input-name-close {
    @include icon-close-red;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate3d(0,-50%,0);
    cursor: pointer;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 35px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--hidden {
      display: none;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__progress-line-wrapper {
    width: 100%;
    height: 7px;
    border: 1px solid $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress-line {
    width: 0;
    height: 100%;
    background: $color-blue;
    animation: fillWidth 1.5s forwards;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__progress-text {
    font: 400 14px/1.571428571428571 $font-default;
    color: $color-gray;
    padding: 0 0 0 16px;
    @include breakpoint($tablet-768) {
      display: none; }
    @include breakpoint($mobile-360); } }
