.info {
  &--first {
    background: url('../img/bg/1.webp') center center no-repeat;
    background-size: cover;
    padding: 180px 0 152px;
    @include breakpoint($tablet-768) {
      padding: 116px 0 150px; }
    @include breakpoint($mobile-360) {
      background: url('../img/bg/1-2.webp') center center no-repeat;
      padding: 105px 0 111px; } }
  &--second {
    padding-bottom: 152px;
    background: none;
    @include breakpoint($tablet-1024) {
      background: none; }
    @include breakpoint($tablet-768) {
      padding-bottom: 56px;
      background: none; }
    @include breakpoint($mobile-360) {
      background: url('../img/bg/1-1.webp') center bottom no-repeat;
      padding-bottom: 74px; } }
  &--third {
    padding-bottom: 154px;
    background: none;
    @include breakpoint($tablet-768) {
      padding-bottom: 83px;
      background: none; }
    @include breakpoint($mobile-360) {
      padding-bottom: 38px;
      background: none; } }
  &__container {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .info--first & {
      @include glass(642px);
      @include breakpoint($tablet-768) {
        max-width: 600px;
        padding-left: 20px;
        padding-right: 20px; }
      @include breakpoint($mobile-360) {
        padding-left: 24px;
        padding-right: 24px; } }
    .info--second & {
      @include glass(750px);
      @include breakpoint($tablet-768) {
        max-width: 750px;
        padding-left: 20px;
        padding-right: 20px; }
      @include breakpoint($mobile-360) {
        padding-left: 15px;
        padding-right: 15px; } }
    &--full-width {
      width: 100%;
      max-width: 100% !important;
      padding-left: 0 !important;
      padding-right: 0 !important;
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; } }
    &--indent-bottom {
      margin: 0 0 51px;
      @include breakpoint($tablet-768) {
        margin: 0 0 51px; }
      @include breakpoint($mobile-360) {
        margin: 0 0 27px; } }
    &--indent-bottom-big {
      margin: 0 0 62px;
      @include breakpoint($tablet-768) {
        margin: 0 0 68px; }
      @include breakpoint($mobile-360); }
    &--text {
      @include glass(1060px);
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: 88px;
      @include breakpoint($tablet-768) {
        max-width: 940px;
        margin-bottom: 58px; }
      @include breakpoint($tablet-640) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; }
      @include breakpoint($mobile-480) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; }
      @include breakpoint($mobile-360-480) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; }
      @include breakpoint($mobile-360) {
        padding-left: 25px;
        padding-right: 25px;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 30px; } }
    &--align-center {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__col {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--left {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      .info--second &,
      .info--third & {
        position: relative;
        width: 50%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        background: $color-pink-light;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          width: 100%; } }
      .info--third & {
        @include breakpoint($tablet-640) {
          width: 100%; }
        @include breakpoint($mobile-480) {
          width: 100%; }
        @include breakpoint($mobile-360-480) {
          width: 100%; }
        @include breakpoint($mobile-360) {
          width: 100%; } }
      .info__container--text & {
        background: none;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
        @include breakpoint($tablet-768);
        @include breakpoint($tablet-640) {
          margin: 0 0 27px; }
        @include breakpoint($mobile-480) {
          margin: 0 0 27px; }
        @include breakpoint($mobile-360-480) {
          margin: 0 0 27px; }
        @include breakpoint($mobile-360) {
          margin: 0 0 27px; } } }
    &--right {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      .info--second &,
      .info--third & {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        background: $color-pink-light;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          width: 100%;
          align-items: center; } }
      .info--third & {
        @include breakpoint($tablet-768);
        @include breakpoint($tablet-640) {
          width: 100%; }
        @include breakpoint($mobile-480) {
          width: 100%; }
        @include breakpoint($mobile-360-480) {
          width: 100%; }
        @include breakpoint($mobile-360) {
          width: 100%; } }
      .info__container--text & {
        background: none;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        padding-left: 123px;
        @include breakpoint($tablet-1024) {
          padding-left: 45px; }
        @include breakpoint($tablet-768) {
          padding-left: 45px; }
        @include breakpoint($tablet-640) {
          padding-left: 0; }
        @include breakpoint($mobile-480) {
          padding-left: 0; }
        @include breakpoint($mobile-360-480) {
          padding-left: 0; }
        @include breakpoint($mobile-360) {
          padding-left: 0; } } } }
  &__img {
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      width: 100%;
      height: auto;
      position: static;
      left: auto;
      right: 0;
      top: auto;
      bottom: auto;
      object-fit: fill; } }
  &__title {
    .info--first & {
      @include title;
      text-align: center;
      margin: 0 0 50px;
      @include breakpoint($tablet-768) {
        margin: 0 0 53px; }
      @include breakpoint($mobile-360) {
        font-size: 22px;
        line-height: 1.171818181818182;
        margin: 0 0 28px; } }
    .info--second & {
      @include title;
      width: 100%;
      padding: 56px 8% 56px 15.384615384615385%;
      @include breakpoint($desktop-1260) {
        font-size: 40px; }
      @include breakpoint($tablet-1024) {
        font-size: 32px;
        width: 83%; }
      @include breakpoint($tablet-768) {
        font-size: 36px;
        line-height: 1.171944444444444; }
      @include breakpoint($mobile-360-480) {
        font-size: 21px; }
      @include breakpoint($tablet-640) {
        font-size: 22px; }
      @include breakpoint($mobile-480) {
        font-size: 18px; }
      @include breakpoint($mobile-360) {
        font-size: 27px;
        line-height: 1.171851851851852;
        text-align: center;
        padding: 48px 0 50px; } }
    .info--third & {
      @include title;
      width: 100%;
      padding: 56px 8% 56px 15.384615384615385%;
      @include breakpoint($desktop-1260) {
        font-size: 40px; }
      @include breakpoint($tablet-1024) {
        font-size: 32px;
        width: 83%; }
      @include breakpoint($tablet-768) {
        font-size: 36px;
        line-height: 1.171944444444444; }
      @include breakpoint($mobile-360-480) {
        font-size: 21px; }
      @include breakpoint($tablet-640) {
        min-height: 46px;
        font-size: 22px; }
      @include breakpoint($mobile-480) {
        min-height: 46px;
        font-size: 18px; }
      @include breakpoint($mobile-360) {
        min-height: 46px;
        font-size: 27px;
        line-height: 1.171851851851852;
        text-align: center;
        padding: 48px 0 50px; } } }
  &__descr {
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    .info--first & {
      @include descr-middle;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-semicircle-blue;
        margin: 50px 0 46px;
        @include breakpoint($tablet-768) {
          margin: 38px 0 38px; }
        //@include breakpoint($mobile-360)
        @include breakpoint($mobile-360) {
          margin: 36px 0 36px;
          width: 18px;
          min-width: 18px;
          height: 11px;
          min-height: 11px; } } }
    .info--second & {
      @include descr-middle;
      text-align: center;
      margin: 0 0 35px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin: 0 0 27px; } }
    .info--third & {
      @include descr-middle;
      margin: 0 0 20px;
      width: 100%;
      max-width: 404px;
      min-height: 56px;
      @include breakpoint($tablet-768) {
        margin: 0 0 17px; }
      @include breakpoint($tablet-640) {
        min-width: 100%;
        min-height: auto;
        margin: 0 0 27px; }
      @include breakpoint($mobile-480) {
        min-width: 100%;
        min-height: auto;
        margin: 0 0 13px; }
      @include breakpoint($mobile-360) {
        min-width: 100%;
        min-height: auto;
        margin: 0 0 13px;
        font-size: 20px !important; }

      //@include breakpoint($mobile-360)
      //  font-size: 14px
 } }      //  line-height: 1.172142857142857
  &__text-wrapper {
    padding: 58px 74px 38px 74px;
    border: 1px solid $color-blue;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      background: $color-white;
      border: none;
      box-shadow: 0 0 14px rgba($color-black, 0.25);
      border-radius: 10px;
      padding: 30px 10px 25px 10px; } }
  &__text {
    @include text;
    @include fluid-type(360px, 768px, 18px, 22px);
    margin: 0 0 45px;
    box-shadow: none;
    text-align: center;
    @include breakpoint($tablet-768) {
      font-size: 18px;
      line-height: 1.171666666666667;
      margin: 0 0 56px; }
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.172142857142857;
      margin: 0 0 40px; }
    .info--first & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin: 0 0 48px; } }
    .info--second & {
      margin-bottom: 0;
      text-align: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin-left: auto;
        margin-right: auto;
        width: 96%; } }
    .info--third & {
      @include text-small;
      text-align: left;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 0 0 0 0;
      width: 100%;
      max-width: 404px;
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640) {
        max-width: 100%; }
      @include breakpoint($mobile-480) {
        font-size: 14px;
        max-width: 100%;
        padding-left: 0;
        padding-right: 0; }
      @include breakpoint($mobile-360) {
        font-size: 14px;
        max-width: 100%;
        padding-left: 0;
        padding-right: 0; }
      &:before {
        content: '';
        @include icon-semicircle-blue-2;
        margin: 0 0 33px;
        @include breakpoint($tablet-768) {
          margin: 0 0 34px; }
        @include breakpoint($tablet-640) {
          margin: 0 0 20px; }
        @include breakpoint($mobile-480) {
          margin: 0 0 20px; }
        @include breakpoint($mobile-360) {
          margin: 0 0 20px; } } }
    &--align-left {
      @include breakpoint($tablet-640) {
        padding-right: 0 !important; }
      @include breakpoint($mobile-480) {
        padding-right: 0 !important; }
      @include breakpoint($mobile-360) {
        padding-right: 0 !important; } }
    &--separator {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        content: '';
        @include icon-semicircle-blue-2;
        margin: 50px 0 46px;
        @include breakpoint($tablet-768) {
          margin: 28px 0 32px 0; }
        @include breakpoint($mobile-360) {
          margin: 18px 0 18px 0; } } }
    .info__container--text .info__col--left & {
      position: relative;
      max-width: 100%;
      padding-right: 40px;
      @include breakpoint($tablet-768) {
        padding-right: 20px; }
      @include breakpoint($tablet-640) {
        padding-right: 0; }
      @include breakpoint($mobile-480) {
        padding-right: 0; }
      @include breakpoint($mobile-360) {
        padding-right: 0; }
      &:after {
        content: '';
        display: block;
        width: 1px;
        height: calc(100% - 30px);
        background: $color-blue;
        position: absolute;
        right: 0;
        bottom: -16px;
        @include breakpoint($tablet-768) {
          display: none; }
        @include breakpoint($tablet-640) {
          display: none; }
        @include breakpoint($mobile-480) {
          display: none; }
        @include breakpoint($mobile-360) {
          display: none; } } }
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768) {
        margin-bottom: 0; }
      @include breakpoint($mobile-360) {
        margin-bottom: 0; } } }
  &__text-bold {
    @include text;
    font-weight: 700;
    @include fluid-type(360px, 768px, 18px, 22px);
    box-shadow: none;
    text-align: center;
    @include breakpoint($tablet-768) {
      font-size: 18px;
      line-height: 1.171666666666667; }
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.172142857142857; } }
  &__text-container-inner {
    max-width: 404px;
    width: 100%;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640) {
      max-width: 100%; }
    @include breakpoint($mobile-480) {
      max-width: 100%; }
    @include breakpoint($mobile-360) {
      max-width: 100%; }
    .info__text--align-left & {
      text-align: left;
      margin: 0 auto 0 0;
      @include breakpoint($tablet-768) {
        padding: 0 0 0 0; }
      @include breakpoint($tablet-640) {
        padding: 0 0 0 0; }
      @include breakpoint($mobile-480) {
        padding: 0 0 0 0; }
      @include breakpoint($mobile-360) {
        padding: 0 0 0 0; } } }
  &__text-outer-wrapper {}
  &__text-inner-wrapper {
    text-align: center;
    vertical-align: top; }
  &__btn {
    @include btn-purple;
    font-size: 19px;
    padding: 22px 65px 22.68px;
    width: auto;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
