.faq {
  padding: 130px 0 220px;
  background: url('../img/bg/2.webp') center center no-repeat;
  background-size: cover;
  @include breakpoint($tablet-768) {
    padding: 49px 0 90px;
    background: url('../img/bg/2-mobile.webp') center center no-repeat; }
  @include breakpoint($mobile-360) {
    padding: 39px 0 53px;
    background: url('../img/bg/2-tablet.webp') center center no-repeat; }
  &__container {
    @include glass(1080px);
    @include breakpoint($tablet-768) {
      padding-left: 90px;
      padding-right: 90px; }
    @include breakpoint($mobile-360) {
      padding-left: 15px;
      padding-right: 15px; } }
  &__title {
    @include title;
    margin: 0 0 16px;
    text-align: center;
    @include breakpoint($tablet-768) {
      margin: 0 0 10px; }
    @include breakpoint($mobile-360) {
      margin: 0 0 22px; } }
  &__descr {
    @include descr-small;
    margin: 0 0 60px;
    text-align: center;
    @include breakpoint($tablet-768) {
      margin: 0 0 50px; }
    @include breakpoint($mobile-360) {
      margin: 0 0 23px; } }
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__list-item {
    margin: 0 0 20px;
    background: $color-white;
    border: 1px solid $color-blue;
    box-sizing: border-box;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    border-radius: 20px;
    width: 100%;
    cursor: pointer;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      border-radius: 10px; }
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--active {
      margin: 0 0 28px;
      border-radius: 10px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        margin: 0 0 22px; }
      &:last-child {
        margin-bottom: 0;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          margin-bottom: 0; } } } }
  &__list-item-title {
    font: 400 18px/1.166666666666667 $font-default;
    color: $color-gray;
    padding: 8.95px 0 10.05px 20px;
    position: relative;
    vertical-align: top;
    text-align: left;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      padding: 9px 13px 14px 8px;
      font-size: 14px;
      line-height: 1.172142857142857; }
    .faq__list-item--active & {
      margin-bottom: 11.5px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &:before {
      content: '';
      display: block;
      width: 30px;
      height: 30px;
      border: 1px solid $color-blue;
      border-radius: 15px;
      position: absolute;
      right: 5px;
      top: 5px;
      background: $color-white;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        width: 14px;
        height: 14px;
        border-radius: 7.5px;
        right: 0;
        top: calc(50% - 1px);
        transform: translate3d(50%, -50%, 0); } }
    &:after {
      content: '';
      @include icon-arrow-left;
      position: absolute;
      top: 14.5px;
      right: 17px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360) {
        top: 50%;
        right: -1.4px;
        transform: translate3d(0,calc(-50% - 0.7px),0); } }
    .faq__list-item--active & {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:before {
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          top: 12px;
          right: 0;
          transform: translate3d(50%, 0, 0); } }
      &:after {
        content: '';
        @include icon-arrow-bottom;
        position: absolute;
        top: 17.8px;
        right: 14.6px;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360) {
          right: 0;
          transform: translate3d(50%,0,0);
          top: 18.3px; } } } }
  &__list-item-title-bold {
    font-weight: 700;
    display: inline;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__list-item-content {
    font: 400 18px/1.166666666666667 $font-default;
    color: $color-gray;
    padding: 0 0 20px 40px;
    width: 84.326923076923077%;
    display: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.172142857142857;
      padding: 0 15px 23px 15px;
      width: 100%; }
    .faq__list-item--active & {
      display: block;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__list-item-content-bold {
    font-weight: 700;
    display: inline;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
