.footer {
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360);
  &--blue-pale {
    background: rgba($color-blue, 0.67);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &--blue {
    background: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &--indent-top-bottom-small {
    padding: 35px 0 36px 0;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &--indent-top-bottom-big {
    padding: 45.06px 0 59.29px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__container {
    @include glass(1380px);
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__col {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--flex-hor {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--width-450 {
      width: 100%;
      max-width: 450px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--width-380 {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-direction: row;
      width: 100%;
      max-width: 380px;
      margin: 0 0 0 auto;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__subsection {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &--indent-left {
      margin: 0 0 0 45px;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__link {
    font: 400 18px/1.166666666666667 $font-default;
    color: $color-white-2;
    margin: 0 0 24px;
    text-decoration: none;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__social-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 25px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__social-item {
    position: relative;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:after {
      content: '';
      display: block;
      width: 1px;
      height: 15px;
      background: rgba($color-blue-5,0.3);
      position: absolute;
      right: -25px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &:last-child {
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360);
      &:after {
        display: none;
        @include breakpoint($tablet-768);
        @include breakpoint($mobile-360); } }
    &--facebook {
      @include icon-facebook;
      margin: 0 50px 0 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--vk {
      @include icon-vk;
      margin: 0 50px 0 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); }
    &--ok {
      @include icon-ok;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__copyright {
    font: 400 18px/1.166666666666667 $font-default;
    color: rgba($color-white-2,0.4);
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__logo {
    @include icon-logo-unilever;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__title {
    font: 700 27px/1.171851851851852 $font-default;
    color: $color-white;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); }
  &__text-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font: 400 18px/1.171666666666667 $font-default;
    color: $color-white;
    margin: 0 0 20px;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360);
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($mobile-360); } }
  &__text-bold {
    font-weight: 700;
    @include breakpoint($tablet-768);
    @include breakpoint($mobile-360); } }
