.code {
  width: 100%;
  height: 100%;
  padding: 136px 0 272px 0;
  @include breakpoint($tablet-1024);
  @include breakpoint($tablet-768);
  @include breakpoint($tablet-640);
  @include breakpoint($mobile-480);
  @include breakpoint($mobile-360-480) {
    padding: 21px 0 21px; }
  @include breakpoint($mobile-360) {
    padding: 21px 0 21px; }
  @include breakpoint($mobile-320) {
    padding: 21px 0 21px; }
  &--bg-blue {
    background: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &--bg-purple {
    background: $color-purple;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__container {
    @include glass(930px);
    @include breakpoint($tablet-768) {
      @include glass(1024px);
      padding-left: 67px;
      padding-right: 67px; }
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480) {
      @include glass(100%);
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($mobile-360-480) {
      @include glass(100%);
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($mobile-360) {
      @include glass(100%);
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($mobile-320) {
      @include glass(100%);
      padding-left: 15px;
      padding-right: 15px; } }
  &__inner-wrapper {
    background: $color-white;
    box-shadow: 0 4px 64px rgba($color-blue-4, 0.3);
    border-radius: 10px;
    padding: 27px 100px 125px;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-360) {
      padding: 15px 20px 35px 20px; }
    @include breakpoint($mobile-320);
    &--indent-bottom-big {
      padding-bottom: 231px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); }
    &--indent-bottom-small {
      padding-bottom: 50px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__title {
    text-align: center;
    font: 700 62px/1.016129032258065 $font-default;
    color: $color-black;
    margin: 0 0 72px;
    @include breakpoint($tablet-768) {
      font-size: 58px; }
    @include breakpoint($tablet-640) {
      font-size: 54px; }
    @include breakpoint($mobile-480) {
      font-size: 52px; }
    @include breakpoint($mobile-360-480) {
      font-size: 40px; }
    @include breakpoint($mobile-360) {
      font-size: 32px; }
    @include breakpoint($mobile-320) {
      font-size: 30px; }
    &--indent-bottom-none {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); }
    &--indent-bottom-small {
      margin-bottom: 62px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__title-selected {
    display: inline-block;
    color: $color-blue;
    position: relative;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    &:after {
      content: ')';
      font: 700 62px/1.016129032258065 $font-default;
      color: $color-blue;
      transform: rotate(90deg);
      position: absolute;
      left: 66px;
      bottom: -50px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360) {
        font-size: 32px;
        left: 34px;
        bottom: -29px; }
      @include breakpoint($mobile-320) {
        font-size: 30px;
        left: 32px;
        bottom: -26px; } }
    &--special {
      &:after {
        bottom: -38px;
        @include breakpoint($mobile-360) {
          font-size: 32px;
          left: 34px;
          bottom: -29px; }
        @include breakpoint($mobile-320) {
          font-size: 30px;
          left: 32px;
          bottom: -26px; } } } }
  &__form {
    width: 100%;
    max-width: 373px;
    margin: 0 auto;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__row {
    margin: 0 0 35px;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__form-text {
    font: italic 400 22px/1 $font-default;
    color: $color-gray;
    margin: 0 0 15px;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480) {
      font-size: 14px;
      line-height: 1.571428571428571; }
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.571428571428571; }
    @include breakpoint($mobile-320); }
  &__text-input {
    @include default-text-input;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360) {
      padding: 11px 13px 11px 13px; }
    @include breakpoint($mobile-320) {
      padding: 11px 13px 11px 13px; } }
  &__form-btn {
    @include btn-blue;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__checkbox-container {
    font: 400 22px/1 $font-default;
    color: $color-gray;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360) {
      font-size: 14px;
      line-height: 1.571428571428571; }
    @include breakpoint($mobile-320); }
  &__checkbox {
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__checkbox-custom {
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__text-link {
    text-decoration: none;
    color: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__progress {
    padding: 175px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    &--success {
      padding-top: 0;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__progress-line-outer {
    border: 1px solid $color-blue;
    width: 100%;
    max-width: 344px;
    height: 7px;
    margin: 0 auto 25px auto;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    .code__progress--success & {
      border: 1px solid $color-green;
      margin: 0 0 10px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__progress-line-inner {
    width: 50%;
    height: 100%;
    background: $color-blue;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    .code__progress--success & {
      background: $color-green;
      width: 100%;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__progress-text {
    font: 400 14px/1.571428571428571 $font-default;
    color: $color-gray;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    .code__progress--success & {
      margin: 0 0 36px;
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); } }
  &__progress-title {
    font: 400 22px/1.227272727272727 $font-default;
    color: $color-black;
    padding: 104px 0 0 0;
    position: relative;
    margin: 0 0 27px;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320);
    &:before {
      content: '';
      @include icon-success;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate3d(-50%,0,0);
      @include breakpoint($tablet-768);
      @include breakpoint($tablet-640);
      @include breakpoint($mobile-480);
      @include breakpoint($mobile-360-480);
      @include breakpoint($mobile-360);
      @include breakpoint($mobile-320); }
    &--mobile-inline {
      @include breakpoint($mobile-360-480) {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-size: 16px; }
      @include breakpoint($mobile-360) {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-top: 0;
        font-size: 16px; }
      @include breakpoint($mobile-320);
      &:before {
        left: auto;
        top: auto;
        position: static;
        width: 63px;
        min-width: 63px;
        min-height: 58px;
        height: 58px; } } }
  &__progress-title-bold {
    font-weight: 700;
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); }
  &__progress-btn {
    @include btn-blue;
    width: 100%;
    max-width: 224px;
    box-shadow: 0 4px 4px rgba($color-black, 0.25);
    @include breakpoint($tablet-768);
    @include breakpoint($tablet-640);
    @include breakpoint($mobile-480);
    @include breakpoint($mobile-360-480);
    @include breakpoint($mobile-360);
    @include breakpoint($mobile-320); } }
