@mixin icon-arrow-left {
  @include icon;
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='m5.756.938-4.21 4.27 4.21 4.272' stroke='%232061E6'/%3E%3C/svg%3E");
  width: 7px;
  min-width: 7px;
  height: 10px;
  min-height: 10px;
  @include breakpoint($tablet-768);
  @include breakpoint($mobile-360) {
    width: 3.5px;
    min-width: 3.5px;
    height: 5px;
    min-height: 5px; } }
